import { computePosition, flip, shift } from '@floating-ui/dom'
import { posToDOMRect, ReactRenderer } from '@tiptap/react'
import { Editor } from '@tiptap/core'
import SuggestionList from './components/SuggestionList'
import { SuggestionOptions } from "@tiptap/suggestion"
import { MentionNodeAttrs } from "@tiptap/extension-mention"

interface Variable {
    id: string
    label: string
}

const updatePosition = (editor: Editor, element: HTMLElement) => {
    const virtualElement = {
        getBoundingClientRect: () => posToDOMRect(editor.view, editor.state.selection.from, editor.state.selection.to),
    }

    computePosition(virtualElement, element, {
        placement: 'bottom-start',
        strategy: 'absolute',
        middleware: [shift(), flip()],
    }).then(({ x, y, strategy }) => {
        element.style.width = 'max-content'
        element.style.position = strategy
        element.style.left = `${x}px`
        element.style.top = `${y}px`
    })
}

let currentVariables: Variable[] = []

export const createSuggestion = (variables: Variable[]): Omit<SuggestionOptions<Variable, MentionNodeAttrs>, "editor"> => {
    currentVariables = variables

    return {
        char: '//',

        items: ({ query }: { query: string }) => {
            return currentVariables
                .filter(item => item.label.toLowerCase().startsWith(query.toLowerCase()))
                .slice(0, 5)
        },

        render: () => {
            let component: ReactRenderer<any, any>

            return {
                onStart: (props: any) => {
                    component = new ReactRenderer(SuggestionList, {
                        props,
                        editor: props.editor,
                    })

                    if (!props.clientRect) {
                        return
                    }

                    component.element.style.position = 'absolute'

                    document.body.appendChild(component.element)

                    updatePosition(props.editor, component.element)
                },

                onUpdate(props: any) {
                    component.updateProps(props)

                    if (!props.clientRect) {
                        return
                    }

                    updatePosition(props.editor, component.element)
                },

                onKeyDown(props: any) {
                    if (props.event.key === 'Escape') {
                        component.destroy()

                        return true
                    }

                    return component.ref?.onKeyDown(props)
                },

                onExit() {
                    component.element.remove()
                    component.destroy()
                },
            }
        },
    }
}

// Funkcja do aktualizacji zmiennych bez tworzenia nowej konfiguracji
export const updateSuggestionVariables = (variables: Variable[]) => {
    currentVariables = variables
}
