import Document from '@tiptap/extension-document'
import Paragraph from '@tiptap/extension-paragraph'
import Text from '@tiptap/extension-text'
import { EditorContent, useEditor } from '@tiptap/react'
import React, { useState, useEffect } from 'react'
import Mention from '@tiptap/extension-mention'
import IdVariable from './tiptap/extensions/idvariable'

import { createSuggestion } from './tiptap/suggestion'
import VariableManager from './components/VariableManager'

interface Variable {
    id: string
    label: string
}

const DEFAULT_VARIABLES: Variable[] = [
    { id: 'CUSTOMER_NAME', label: 'Nazwa klienta' },
    { id: 'COMPANY_NAME', label: 'Nazwa firmy' },
    { id: 'PROJECT_NAME', label: 'Nazwa projektu' },
    { id: 'EMAIL', label: 'Adres email' },
    { id: 'PHONE', label: 'Numer telefonu' },
    { id: 'DATE', label: 'Data' },
    { id: 'PRODUCT_NAME', label: 'Nazwa produktu' },
    { id: 'PRICE', label: 'Cena' },
]

// Rozszerzenie typów dla komend edytora
declare module '@tiptap/core' {
    interface Commands<ReturnType> {
        idVariable: {
            getPlainText: () => string
        }
    }
}

export default () => {
    const [variables, setVariables] = useState<Variable[]>(DEFAULT_VARIABLES)
    const [copySuccess, setCopySuccess] = useState(false)

    const editor = useEditor({
        extensions: [
            Document,
            Paragraph,
            Text,
            Mention.configure({
                HTMLAttributes: {
                    class: 'mention',
                },
                renderLabel: ({ node }) => `${node.attrs.label}`,
                deleteTriggerWithBackspace: true,
                suggestion: createSuggestion(variables)
            }),
            IdVariable.configure({
                validIds: variables,
                className: 'mention'
            })
        ],
        content: `<p>Rozpocznij pisanie...</p>`,
    });

    // Tylko, jeżeli zmienne się zmienią, zaktualizuj konfigurację Mention
    // Można wywalić ogólnie
    useEffect(() => {
        if (editor) {
            // Aktualizuj konfigurację Mention z nowymi zmiennymi
            editor.extensionManager.extensions.forEach(extension => {
                if (extension.name === 'mention') {
                    extension.options.suggestion = createSuggestion(variables)
                }
                if (extension.name === 'idVariable') {
                    extension.options.validIds = variables
                }
            })

            // Przeładuj konfigurację edytora
            editor.commands.clearContent()
            editor.commands.setContent(`<p>Rozpocznij pisanie...</p>`)
        }
    }, [variables, editor])

    const copyToClipboard = async () => {
        if (editor) {
            const text = editor.commands.getPlainText()
            try {
                await navigator.clipboard.writeText(text)
                setCopySuccess(true)
                setTimeout(() => setCopySuccess(false), 2000)
            } catch (err) {
                console.error('Failed to copy text: ', err)
            }
        }
    }

    if (!editor) {
        return null
    }

    return (
        <div className="app-container">
            <header className="app-header">
                <h1>Prompt Editor</h1>
                <p>Wpisz <code>//</code>, aby dodać zmienną</p>
            </header>

            <div className="main-content">
                <aside className="sidebar">
                    <VariableManager
                        variables={variables}
                        onVariablesChange={setVariables}
                    />
                </aside>

                <main className="editor-container">
                    <div className="editor-wrapper">
                        <div className="editor-header">
                            <button
                                onClick={copyToClipboard}
                                className={`copy-button ${copySuccess ? 'success' : ''}`}
                            >
                                {copySuccess ? '✓ Skopiowano' : 'Copy output'}
                            </button>
                        </div>
                        <EditorContent editor={editor} />
                    </div>
                </main>
            </div>
        </div>
    )
}