import React, { useState } from 'react'

interface Variable {
    id: string
    label: string
}

interface VariableManagerProps {
    variables: Variable[]
    onVariablesChange: (variables: Variable[]) => void
}

export default function VariableManager({ variables, onVariablesChange }: VariableManagerProps) {
    const [newVariable, setNewVariable] = useState('')
    const [newVariableId, setNewVariableId] = useState('')

    const generateIdFromLabel = (label: string) => {
        return label.toUpperCase().replace(/\s+/g, '_').replace(/[^A-Z0-9_]/g, '')
    }

    const addVariable = () => {
        if (newVariable.trim()) {
            const label = newVariable.trim()
            const id = newVariableId.trim() || generateIdFromLabel(label)

            // Sprawdź czy ID już istnieje
            if (variables.some(v => v.id === id)) {
                alert('Zmienna o tym ID już istnieje!')
                return
            }

            onVariablesChange([...variables, { id, label }])
            setNewVariable('')
            setNewVariableId('')
        }
    }

    const removeVariable = (id: string) => {
        onVariablesChange(variables.filter(v => v.id !== id))
    }

    const updateVariable = (oldId: string, newLabel: string, newId: string) => {
        if (!newLabel.trim() || !newId.trim()) return

        // Sprawdź czy nowe ID już istnieje (ale nie dla tej samej zmiennej)
        if (newId !== oldId && variables.some(v => v.id === newId)) {
            alert('Zmienna o tym ID już istnieje!')
            return
        }

        onVariablesChange(variables.map(v =>
            v.id === oldId
                ? { id: newId.trim(), label: newLabel.trim() }
                : v
        ))
    }

    const handleKeyPress = (e: React.KeyboardEvent) => {
        if (e.key === 'Enter') {
            addVariable()
        }
    }

    // Auto-generuj ID gdy użytkownik wpisuje nazwę
    const handleLabelChange = (value: string) => {
        setNewVariable(value)
        if (!newVariableId) {
            setNewVariableId(generateIdFromLabel(value))
        }
    }

    return (
        <div className="variable-manager">
            <h3>Zmienne</h3>

            <div className="add-variable">
                <div className="variable-form">
                    <input
                        type="text"
                        value={newVariable}
                        onChange={(e) => handleLabelChange(e.target.value)}
                        onKeyPress={handleKeyPress}
                        placeholder="Nazwa zmiennej..."
                        className="variable-input"
                    />
                    <div style={{ height: '0.5rem' }}></div>
                    <input
                        type="text"
                        value={newVariableId}
                        onChange={(e) => setNewVariableId(e.target.value.toUpperCase())}
                        onKeyPress={handleKeyPress}
                        placeholder="ID zmiennej..."
                        className="variable-input variable-id-input"
                    />
                </div>
                <button onClick={addVariable} className="add-button">
                    Dodaj
                </button>
            </div>

            <div className="variables-list">
                {variables.map(variable => (
                    <VariableItem
                        key={variable.id}
                        variable={variable}
                        onUpdate={updateVariable}
                        onRemove={removeVariable}
                    />
                ))}
            </div>
        </div>
    )
}

interface VariableItemProps {
    variable: Variable
    onUpdate: (oldId: string, newLabel: string, newId: string) => void
    onRemove: (id: string) => void
}

function VariableItem({ variable, onUpdate, onRemove }: VariableItemProps) {
    const [isEditing, setIsEditing] = useState(false)
    const [editLabel, setEditLabel] = useState(variable.label)
    const [editId, setEditId] = useState(variable.id)

    const handleSave = () => {
        onUpdate(variable.id, editLabel, editId)
        setIsEditing(false)
    }

    const handleCancel = () => {
        setEditLabel(variable.label)
        setEditId(variable.id)
        setIsEditing(false)
    }

    const handleKeyPress = (e: React.KeyboardEvent) => {
        if (e.key === 'Enter') {
            handleSave()
        } else if (e.key === 'Escape') {
            handleCancel()
        }
    }

    if (isEditing) {
        return (
            <div className="variable-item editing">
                <div className="variable-edit-form">
                    <input
                        type="text"
                        value={editLabel}
                        onChange={(e) => setEditLabel(e.target.value)}
                        onKeyPress={handleKeyPress}
                        className="variable-edit-input"
                        placeholder="Nazwa zmiennej..."
                        autoFocus
                    />
                    <input
                        type="text"
                        value={editId}
                        onChange={(e) => setEditId(e.target.value.toUpperCase())}
                        onKeyPress={handleKeyPress}
                        className="variable-edit-input variable-id-edit"
                        placeholder="ID zmiennej..."
                    />
                </div>
                <div className="variable-actions">
                    <button onClick={handleSave} className="save-button">
                        ✓
                    </button>
                    <button onClick={handleCancel} className="cancel-button">
                        ✕
                    </button>
                </div>
            </div>
        )
    }

    return (
        <div className="variable-item">
            <div className="variable-info">
                <span className="variable-label">{variable.label}</span>
                <span className="variable-id">ID: {variable.id}</span>
            </div>
            <div className="variable-actions">
                <button
                    onClick={() => setIsEditing(true)}
                    className="edit-button"
                    title="Edytuj"
                >
                    ✎
                </button>
                <button
                    onClick={() => onRemove(variable.id)}
                    className="remove-button"
                    title="Usuń"
                >
                    ×
                </button>
            </div>
        </div>
    )
}
