import { Node, mergeAttributes } from '@tiptap/core'
import { Plugin, PluginKey } from '@tiptap/pm/state'
import { Transaction, EditorState } from '@tiptap/pm/state'
import { Node as ProseMirrorNode, Schema } from '@tiptap/pm/model'

interface ValidId {
    id: string
    label: string
}

interface IdVariableOptions {
    validIds: ValidId[]
    className: string
    style: string
}

interface IdVariableAttributes {
    id: string
    label: string
}

interface Replacement {
    from: number
    to: number
    id: string
    label: string | undefined
    nodePos: number
    nodeSize: number
}

// Custom node for ID variables
// @ts-ignore
export default Node.create<IdVariableOptions>({
    name: 'idVariable',

    group: 'inline',
    inline: true,
    selectable: false,
    atom: true,

    addOptions() {
        return {
            // List of valid IDs with their labels
            // Format: [{ id: 'userId', label: 'User ID' }, { id: 'orderNumber', label: 'Order #' }]
            validIds: [],

            // Custom styling
            className: 'mention',
            style: ''
        }
    },

    addAttributes() {
        return {
            id: {
                default: '',
                parseHTML: (element: HTMLElement) => element.getAttribute('data-id'),
                renderHTML: (attributes: IdVariableAttributes) => ({
                    'data-id': attributes.id,
                }),
            },
            label: {
                default: '',
                parseHTML: (element: HTMLElement) => element.getAttribute('data-label'),
                renderHTML: (attributes: IdVariableAttributes) => ({
                    'data-label': attributes.label,
                }),
            },
        }
    },

    parseHTML() {
        return [
            {
                tag: 'span[data-id]',
                getAttrs: (element: HTMLElement) => ({
                    id: element.getAttribute('data-id'),
                    label: element.getAttribute('data-label') || element.getAttribute('data-id'),
                }),
            },
        ]
    },

    renderHTML({ HTMLAttributes, node }: { HTMLAttributes: Record<string, any>, node: ProseMirrorNode }) {
        const { id, label } = node.attrs as IdVariableAttributes
        return ['span', mergeAttributes(HTMLAttributes, {
            class: this.options.className,
            'data-id': id,
            'data-label': label,
            'data-type': this.options.className,
            contenteditable: 'false',
        }), label || id]
    },

    addProseMirrorPlugins() {
        const validIdsMap = new Map(
            this.options.validIds.map((item: ValidId) => [item.id, item.label])
        )

        return [
            new Plugin({
                key: new PluginKey('idVariablePlugin'),

                // Handle every text change - this is the key addition
                appendTransaction: (transactions: readonly Transaction[], oldState: EditorState, newState: EditorState) => {
                    let tr: Transaction | null = null
                    let shouldProcess = false

                    // Check if any transaction changed the document
                    transactions.forEach(transaction => {
                        if (transaction.docChanged) {
                            shouldProcess = true
                        }
                    })

                    if (!shouldProcess) return null

                    try {
                        const replacementsToMake: Replacement[] = []

                        // Collect all replacements first to avoid position conflicts
                        newState.doc.descendants((node: ProseMirrorNode, pos: number) => {
                            if (node.type.name === 'text' && node.text) {
                                const text = node.text
                                const regex = /\{\{([^}]+)\}\}/g
                                let match: RegExpExecArray | null

                                while ((match = regex.exec(text)) !== null) {
                                    const id = match[1].trim()

                                    // Only convert if ID is in the valid list
                                    if (validIdsMap.has(id)) {
                                        replacementsToMake.push({
                                            from: pos + match.index,
                                            to: pos + match.index + match[0].length,
                                            id: id,
                                            label: validIdsMap.get(id),
                                            nodePos: pos,
                                            nodeSize: node.nodeSize
                                        })
                                    }
                                }
                            }
                        })

                        // Sort replacements by position (descending) to avoid offset issues
                        replacementsToMake.sort((a, b) => b.from - a.from)

                        // Apply replacements
                        if (replacementsToMake.length > 0) {
                            tr = newState.tr

                            replacementsToMake.forEach(replacement => {
                                // Verify the position is still valid
                                if (replacement.from >= 0 &&
                                    replacement.to <= newState.doc.content.size &&
                                    replacement.from < replacement.to) {

                                    try {
                                        const idVariableNode = newState.schema.nodes.idVariable.create({
                                            id: replacement.id,
                                            label: replacement.label
                                        })

                                        tr!.replaceWith(replacement.from, replacement.to, idVariableNode)
                                    } catch (replaceError) {
                                        console.warn('Failed to replace text with ID variable:', replaceError)
                                    }
                                }
                            })
                        }
                    } catch (error) {
                        console.warn('Error in appendTransaction:', error)
                        return null
                    }

                    return tr
                }
            })
        ]
    },

    // @ts-ignore
    addCommands() {
        return {
            getPlainText: () => ({ state }: { state: EditorState }) => {
                let result = ''

                state.doc.descendants((node: ProseMirrorNode) => {
                    if (node.type.name === 'text') {
                        result += node.text
                    } else if (node.type.name === 'mention' || node.type.name === 'idVariable') {
                        result += `{{${node.attrs.id}}}`
                    } else if (node.isBlock && result && !result.endsWith('\n')) {
                        result += '\n'
                    } else if (node.type.name === 'hardBreak') {
                        result += '\n'
                    }
                })

                return result.trim()
            },
        }
    }
})